export class User {
    constructor(id, login, password, role) {
        this.id = id;
        this.login = login;
        this.password = password;
        this.role = role;
    }
}
export function mapToUser(value) {
    return new User(value.id, value.login, value.password, value.role);
}
export function mapToUsers(value) {
    return value.map(mapToUser);
}
