export class SidebarItem {
    constructor(text, url, icon, innerItems) {
        this.text = text;
        this.url = url;
        this.icon = icon;
        this.innerItems = innerItems;
    }
}
export function mapToSidebarItem(data) {
    const innerItems = data.innerItems ? data.innerItems.map(i => mapToSidebarItem(i)) : [];
    return new SidebarItem(data.text, data.url, data.icon, innerItems);
}
export function mapToSidebarItems(value) {
    return value.map(mapToSidebarItem);
}
