import { mapAnswerOptionGroups } from "./answerOption/answerOptionGroups/answerOptionGroup";
import { TestItem } from "./testItem";
export class ComparisonItem extends TestItem {
    constructor(id, testId, type, question, answerOptionGroups) {
        super(id, testId, type, question);
        this.id = id;
        this.testId = testId;
        this.type = type;
        this.question = question;
        this.answerOptionGroups = answerOptionGroups;
    }
}
export function mapToComparisonItem(value) {
    const answerOptions = mapAnswerOptionGroups(value.answerOptionGroups);
    return new ComparisonItem(value.id, value.testId, value.type, value.question, answerOptions);
}
