import { __awaiter } from "tslib";
import { HttpRequest } from "tools/httpClient/httpClient";
import { PagedResult } from "tools/results/pagedResult";
import { mapToTestItems } from "./items/testItemsUtils";
import { mapToTest } from "./test";
export class TestsProvider {
    static saveTest(testBlank, testItemBlanks) {
        return __awaiter(this, void 0, void 0, function* () {
            const result = yield HttpRequest.post("/Tests/Save").withBody({ testBlank, testItemBlanks }).asResult();
            return result;
        });
    }
    static getTest(id) {
        return __awaiter(this, void 0, void 0, function* () {
            const result = yield HttpRequest.get("/Tests/GetTest").withQueryParams({ id }).asAny();
            return mapToTest(result);
        });
    }
    static getPagedTests(page, count) {
        return __awaiter(this, void 0, void 0, function* () {
            const result = yield HttpRequest.get("/Tests/GetPaged").withQueryParams({ page, count }).asAny();
            return PagedResult.convert(result, mapToTest);
        });
    }
    static removeTest(id) {
        return __awaiter(this, void 0, void 0, function* () {
            const result = yield HttpRequest.post("/Tests/Remove").withBody(id).asResult();
            return result;
        });
    }
    static geTestItems(testId) {
        return __awaiter(this, void 0, void 0, function* () {
            const result = yield HttpRequest.get("/Tests/GetItems").withQueryParams({ testId }).asAny();
            return mapToTestItems(result);
        });
    }
}
