import { mapToSidebarItem } from './sidebarItem';
export default class SidebarLinksTree {
    static loadSidebar() {
        const sidebarLinksTree = window.sidebarLinksTree;
        if (sidebarLinksTree == null || sidebarLinksTree == undefined)
            return;
        const items = sidebarLinksTree.map(d => mapToSidebarItem(d));
        SidebarLinksTree.items = items;
        SidebarLinksTree.itemsWithoutTree = SidebarLinksTree.getItemsWithoutTree(items);
    }
    static getItemsWithoutTree(items) {
        return items.flatMap(i => i.innerItems).concat(items);
    }
}
SidebarLinksTree.items = [];
SidebarLinksTree.itemsWithoutTree = [];
SidebarLinksTree.loadSidebar();
