import { jsx as _jsx } from "react/jsx-runtime";
import { TestItemType } from "domain/tests/items/testItemType";
import { RadioButtonsItemEditor } from "./radioButtonsItemEditor";
import { NumberFieldTestItemEditor } from "./numberFieldTestItemEditor";
import { TextFieldTestItemEditor } from "./textFieldTestItemEditor";
import { CheckBoxesItemEditor } from "./checkBoxesItemEditor";
import { ComparisonItemEditor } from "./comparisonItemEditor";
export function TestItemEditor({ item, changeItem }) {
    switch (item.type) {
        case TestItemType.TextField:
            return _jsx(TextFieldTestItemEditor, { item: item, changeItem: changeItem });
        case TestItemType.NumericField:
            return _jsx(NumberFieldTestItemEditor, { item: item, changeItem: changeItem });
        case TestItemType.RadioButtonsGroup:
            return _jsx(RadioButtonsItemEditor, { item: item, changeItem: changeItem });
        case TestItemType.CheckboxesGroup:
            return _jsx(CheckBoxesItemEditor, { item: item, changeItem: changeItem });
        case TestItemType.Comparison:
            return _jsx(ComparisonItemEditor, { item: item, changeItem: changeItem });
        default:
            return null;
    }
}
