import { useEffect } from 'react';
const emptyDeps = [];
export default function useComponent(config) {
    useEffect(() => {
        const state = { cleanupFunction: false };
        if (config.didMount)
            config.didMount(state);
        return () => {
            state.cleanupFunction = true;
            if (config.didUnmount)
                config.didUnmount();
        };
    }, emptyDeps);
}
