export class TestBlank {
    constructor(id, userId, title) {
        this.id = id;
        this.userId = userId;
        this.title = title;
    }
    static getDefault() {
        return new TestBlank(null, null, null);
    }
    static formTest(test) {
        return new TestBlank(test.id, test.userId, test.title);
    }
}
